import React from 'react';
import {connect} from 'react-redux';
import FirstView from './FirstView';
import FirstLevel from './FirstLevel';
import FirstLevelFail from './FirstLevelFail';
import FirstLevelPass from './FirstLevelPass';
import './App.css';
import SecondLevel from './SecondLevel'


class App extends React.Component {
	constructor(props) {
		super(props);
	}	
	
	
	
	
	
	render() {
		
			
			
			return (
			<div id='mainView' draggable='false'>
				{this.props.gameLevel=='firstView' && <FirstView/>}
				{this.props.gameLevel=='firstLevel' && <FirstLevel/>}
				{this.props.gameLevel=='firstLevelFail' && <FirstLevelFail/>}
				{this.props.gameLevel=='firstLevelPass' && <FirstLevelPass/>}
				{this.props.gameLevel=='secondLevel' && <SecondLevel/>}
			</div>
			)
				
			}
		
	
	
}

function mapStateToProps(state){
	return {
			gameLevel:state.gameLevel,
			monsterCollection:state.monsterCollection,
			moveProperties:state.moveProperties,
			jetPosition:state.jetPosition,
			arrowPositions:state.arrowPositions,
	}
}
	

export default connect (mapStateToProps) (App)